// React
import React from 'react';

// Custom button
import CustomButton from '../custom-button/CustomButton';

// Firebase
import { signInWithGoogle } from '../../components/firebase/firebase.utils';

// Material UI
import { Grid, Typography, TextField, Icon } from '@material-ui/core';

class SignIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      emai: '',
      password: '',
    };
  }

  handleSubmit = (event) => {
    event.preventDefault();

    this.setState({ email: '', password: '' });
  };

  handleChange = (event) => {
    const { value, name } = event.target;

    this.setState({ [name]: value });
  };

  render() {
    return (
      <Grid container justify='center'>
        <Grid item style={{ marginTop: '5em' }}>
          <Typography variant='h2'>J谩 tenho uma conta</Typography>
          <Typography variant='h3'>
            Entra com seu email e contrasenha
          </Typography>
          <form onSubmit={this.handleSubmit}>
            <TextField
              fullWidth
              name='email'
              value={this.state.email || ''}
              type='email'
              autoComplete='on'
              label='Email'
              variant='outlined'
              onChange={this.handleChange}
              required
              md={6}
              xs={12}
              style={{ marginTop: '2em' }}
            />
            <TextField
              fullWidth
              name='password'
              id='password'
              type='password'
              autoComplete='on'
              label='Password'
              variant='outlined'
              onChange={this.handleChange}
              value={this.state.password || ''}
              required
              md={6}
              xs={12}
              style={{ marginTop: '2em' }}
            />
            <Grid container justify='space-between'>
              <CustomButton
                variant='contained'
                type='submit'
                value='submit-form'
                color='secondary'
              >
                Sign in
              </CustomButton>
              <CustomButton
                variant='contained'
                onClick={signInWithGoogle}
                value='submit-form'
                color='primary'
                style={{ marginLeft: '1em' }}
                startIcon={<Icon className='fab fa-google' />}
              >
                Sign in with Google
              </CustomButton>
            </Grid>
          </form>
        </Grid>
      </Grid>
    );
  }
}

export default SignIn;
